import React from "react"
import './styles.scss'

class Currency extends React.Component {
    state = {
        dateCBR: '',
        usd: '',
        rmb: '',
        eur: ''
    }

    componentDidMount() {
        fetch('https://www.cbr-xml-daily.ru/daily_json.js', {
            method: "GET",
          }).then(
            data => {
              return  data.json() 
            }
          ).then(res => {
            console.log(res)
            this.setState({
                dateCBR: res.Date,
                usd: res.Valute.USD.Value,
                rmb: res.Valute.CNY.Value,
                eur: res.Valute.EUR.Value
            })
          })
        }
          

    render() {
        return (
            <>
                <div id='currency' className="pa3" title={`Курс указан по состоянию на ${new Date(this.state.dateCBR)}`}>
                    <div className="pa2 fw6" >{`CNY ЦБ ${this.state.rmb}`}</div>
                    <div className="pa2 fw6">{`USD ЦБ ${this.state.usd}`}</div>
                    <div className="pa2 fw6">{`EUR ЦБ ${this.state.eur}`}</div> 
                </div>
            </>
        )
    }
}

export default Currency